'use client';

import React, { useState,useEffect } from 'react';
import axios from 'axios';
import NewCouponModal from '../../../components/NewCouponModal'; // ÏúÑÏπò ÌôïÏù∏ ÌïÑÏàò
import { PlusIcon, TrashIcon } from 'lucide-react';



export default function CouponsPage() {
  /*const [coupons, setCoupons] = useState([
    { id: 1, name: 'WELCOME10', discount: 10, validUntil: '2025-12-31' },
    { id: 2, name: 'SPRING20', discount: 20, validUntil: '2025-06-30' },
    { id: 3, name: 'SUMMER15', discount: 15, validUntil: '2025-08-31' },
  ]);*/
// ÎÇ†Ïßú Ìè¨Îß∑ Ìï®Ïàò Ï∂îÍ∞Ä
  const formatDate = (datetime) => {
    return new Date(datetime).toISOString().split('T')[0]; // "2025-07-11"
  };

  const [coupons, setCoupons] = useState([]);
  const [showModal, setShowModal] = useState(false);

  const fetchCoupons = async () => {
    try {
      const token = localStorage.getItem("accessToken");

      const response = await axios.get('http://localhost:8080/api/admin/coupons', {
        headers: {
          Authorization: `Bearer ${token}`, // ‚úÖ Ïò¨Î∞îÎ•∏ ÌÖúÌîåÎ¶ø Î¨∏ÏûêÏó¥
          "Content-Type": "application/json"
        },
      });

      setCoupons(response.data.data)
      } catch (error) {
      console.error('‚ùå Ïø†Ìè∞ Î™©Î°ù Ï°∞Ìöå Ïã§Ìå®:', error);
    }
  };



  useEffect(() => {
    fetchCoupons();
  }, []);

  const handleAdd = async (couponData) => {
    try {
      console.log("üì¶ Î≥¥ÎÇº couponData:", couponData); // ÌôïÏù∏Ïö©
      const token = localStorage.getItem('accessToken');

      await axios.post('http://localhost:8080/api/admin/coupons', couponData, {
        headers: {
          Authorization: `Bearer ${token}`,
          'Content-Type': 'application/json',
        },
      });

      setShowModal(false);
      fetchCoupons();
    } catch (error) {
      console.error('‚ùå Ïø†Ìè∞ Îì±Î°ù Ïã§Ìå®:', error);
    }
  };




  const handleDelete = async (id) => {
    try {
      await axios.delete(`/api/coupons/${id}`);
      fetchCoupons(); // ÏÇ≠Ï†ú ÌõÑ Î™©Î°ù Í∞±Ïã†
    } catch (error) {
      console.error('‚ùå Ïø†Ìè∞ ÏÇ≠Ï†ú Ïã§Ìå®:', error);
    }
  };


  return (
    <div className="space-y-6">
      <div className="flex items-center justify-between">
        <h1 className="text-2xl font-bold">Ïø†Ìè∞ Í¥ÄÎ¶¨</h1>
        <button
          onClick={() => setShowModal(true)}
          className="bg-[#9BCC47] text-white px-4 py-2 rounded-md flex items-center"
        >
          <PlusIcon size={16} className="mr-1" />
          Ïø†Ìè∞ Îì±Î°ù
        </button>
      </div>

      <div className="bg-white border rounded shadow-sm">
        <table className="w-full">
          <thead>
            <tr className="text-xs text-gray-500 uppercase bg-gray-50">
              <th className="px-4 py-2">Ïø†Ìè∞Î™Ö</th>
              <th className="px-4 py-2">Ìï†Ïù∏Ïú®(%)</th>
              <th className="px-4 py-2">Ïú†Ìö®Í∏∞Í∞Ñ</th>
              <th className="px-4 py-2">Í¥ÄÎ¶¨</th>
            </tr>
          </thead>
          <tbody className="divide-y divide-gray-200">
            {coupons.map(c => (
              <tr key={c.id}>
                <td className="px-4 py-2">{c.name}</td>
                <td className="px-4 py-2">{c.discount}</td>
                <td className="px-4 py-2">{formatDate(c.validUntil)}</td>
                <td className="px-4 py-2">
                <button
                    onClick={() => handleDelete(c.id)}
                    className="text-red-600 hover:text-red-800"
                  >
                    <TrashIcon size={18} />
                  </button>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>

      {showModal && (
        <NewCouponModal onAdd={handleAdd} onClose={() => setShowModal(false)} />
      )}
    </div>
  );
}
